<template>
<el-container v-loading="loading">
  <div class="role-new">
    <div class="list">
      <span>角色名称：</span>
      <span>{{sign}}</span>
    </div>
    <div class="list manage">
      <span>权限管理：</span>
      <div class="right">
        <div class="title">商品管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="goods in goodsList" :label="goods" :key="goods">{{goods}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">订单管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="hosNet in orderList" :label="hosNet" :key="hosNet">{{hosNet}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">活动管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="userManage in activeList" :label="userManage" :key="userManage">{{userManage}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">公告管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="wordBook in noticeList" :label="wordBook" :key="wordBook">{{wordBook}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">媒体管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="system in mediaList" :label="system" :key="system">{{system}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">学员管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="system in studentsList" :label="system" :key="system">{{system}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">校区管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="system in schoolList" :label="system" :key="system">{{system}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">服务管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="system in serveList" :label="system" :key="system">{{system}}</el-checkbox>
        </el-checkbox-group>

        <div class="title">系统管理</div>
        <el-checkbox-group v-model="companyCheck">
            <el-checkbox v-for="system in systemList" :label="system" :key="system">{{system}}</el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
    <div class="list submit">
      <el-button type="primary" @click="submit">提交</el-button>
      <el-button @click="cancle">取消</el-button>
    </div>
  </div>
</el-container>
</template>
<script>
import menuData from "data/menu";
export default {
  name: "roleNew",
  data() {
    return {
      sign: "",
      name: "",
      companyCheck: [],
      id: "",
      type: "",
      loading: "",
      goodsList: ["商品管理"],
      orderList: ["订单管理"],
      activeList: ["活动管理"],
      noticeList: ["公告管理"],
      mediaList: ["媒体管理"],
      studentsList: ["学员列表", "积分复核", "积分限制"],
      schoolList: ["校区列表", "班级列表"],
      serveList: ["服务列表", "问卷列表"],
      systemList: ["权限管理", "用户管理", "邀请管理"]
    };
  },
  mounted() {
    this.editInit();
  },
  methods: {
    editInit() {
      let { type, id } = this.$route.query;
      if (!id) return;
      this.loading = true;
      this.type = type;
      this.id = id;
      this.api.get(`/role/${id}`).then(res => {
          //查询产品详情
          this.dataInit(res.data);
          this.loading = false;
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    dataInit(data) {
      data = data || {};
      let { menus, sign, name } = data;
      this.sign = sign;
      this.name = name;
      if (!menus || this.lodash.isArray(menus)) return;
      let menuDatas = JSON.parse(menus);
      let companyCheck = [];
      this.lodash.forEach(menuDatas, item => {
        //一级菜单
        if (!item.childs) companyCheck.push(item.entity.alias);
        //二级菜单
        this.lodash.forEach(item.childs, menu => {
          companyCheck.push(menu.entity.alias);
        });
      });
      this.companyCheck = companyCheck;
    },
    submit() {
      let datas = JSON.parse(JSON.stringify(menuData));
      let arryData = [];
      let checkList = [].concat(this.companyCheck);
      //一级菜单
      const datasFirst = this.lodash.filter(datas.childs, item => {
        return this.lodash.includes(checkList, item.entity.alias);
      });
      //二级菜单
      const datasScond = this.lodash.filter(datas.childs, item => {
        item.childs = this.lodash.filter(item.childs, child => {
          return this.lodash.includes(checkList, child.entity.alias);
        });
        return item.childs.length > 0;
      });
      //菜单列表
      datas = [].concat(datasFirst, datasScond);
      //过滤childs 为空
      datas = this.lodash.map(datas, item => {
        if(item.childs.length < 1){
            item = this.lodash.omit(item, 'childs')
        }
        return item;
      });
      let param = {
        name: this.id,
        sign: this.sign,
        menus: JSON.stringify(datas)
      };
      this.editUser(param);
    },
    editUser(param) {
      //修改用户
      param.id = this.id;
      this.loading = true;
      this.api.put("/role", param).then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    cancle() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.role-new {
  padding-top: 20px;
  padding-left: 20px;
  .el-input {
    width: 300px;
  }
  .el-radio,
  .el-checkbox {
    height: 40px;
    line-height: 40px;
  }
  .list {
    display: flex;
    height: 40px;
    line-height: 40px;
    span {
      display: inline-block;
      height: 40px;
      line-height: 40px;
    }
    .list-text {
      display: inline-block;
      font-size: 12px;
      margin-left: 40px;
      margin-top: 1px;
      color: gray;
    }
    &.manage {
      height: inherit;
      line-height: inherit;
      .title {
        height: 40px;
        line-height: 40px;
        font-weight: 600;
      }
    }
    &.submit {
      display: inline-block;
      text-indent: 5em;
      margin-top: 10px;
    }
  }
}
</style>